let Web3 = require('web3');
let web3 = new Web3(Web3.givenProvider);
let request = require('superagent');
const approvalHash = "0x095ea7b3";
const unlimitedAllowance = "ffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff";
const { ERC20ABI, ERC721ABI } = require("./ABI.js");

export function getQuery(chainId, address) {
    //console.log(chainId);
    switch (chainId) {
        case 1285:
            return "https://blockscout.moonriver.moonbeam.network/api/?module=account&action=txlist&address=" + address;
        default:
            return "";
    }
}

export function getEtherScanPage(chainId) {
    switch (chainId) {
        case 1285:
            return "https://blockscout.moonriver.moonbeam.network/address/";
        default:
            return "";
    }
}

export async function getApproveTransactions(query) {
    try {
        let data = await request.get(query);
        let approveTransactions = [];
        let dataObj = JSON.parse(data.text).result;

        //console.log("etherscan api return ", dataObj);
        for (let tx of dataObj) {
            if (tx.input.includes(approvalHash)) {
                //console.log("found approve transaction")
                let approveObj = {};
                approveObj.contract = web3.utils.toChecksumAddress(tx.to);
                approveObj.approved = web3.utils.toChecksumAddress("0x" + tx.input.substring(34, 74));
                let allowance = tx.input.substring(74);
                if (allowance.includes(unlimitedAllowance)) {
                    approveObj.allowance = "unlimited";
                } else {
                    approveObj.allowance = "some";
                    approveObj.allowanceUnEdited = allowance;
                }
                if (parseInt(allowance, 16) !== 0) {
                    approveTransactions.push(approveObj);
                } else {
                    //console.log("clean object");
                    approveObj.allowance = "CLEAN";
                    approveObj.allowanceUnEdited = allowance;
                    approveTransactions.push(approveObj);
                    // TODO clean up
                    // Remove all previous additions of this approval transaction as it is now cleared up   

                }
            }
        }

        return approveTransactions;
    } catch (e) {
        throw e;
    }
}

export async function getName(contractAddress) {

    try {
        let contract = new web3.eth.Contract(ERC20ABI, contractAddress);
        return await contract.methods.name().call();
    } catch (e) {
        // name not found, just use contract address
        if (contractAddress === "0x78Aa55Ce0b0DC7488d2C38BD92769f4d0C8196Ff") {
            return "MoonFarm"
        }
        console.error(e);
        return contractAddress;
    }
}

export async function is721(contractAddress, tokenId) {
    let contract = new web3.eth.Contract(ERC721ABI, contractAddress);
    try {
        const _ = await contract.methods.ownerOf(tokenId).call();
        return true; // if this call passes, it must be ERC721
    } catch (e) {
        // method doesn't exist, can't be 721
        return false;
    }
}

const provider = window.ethereum
const moonriverChainId = '0x505';

export const supportedNetworks = {
    moonriver: {
        chainId: moonriverChainId,
        chainName: 'Moonriver',
        rpcUrls: ['https://rpc.moonriver.moonbeam.network'],
        blockExplorerUrls: ['https://blockscout.moonriver.moonbeam.network/'],
        nativeCurrency: {
            name: 'Moonriver',
            symbol: 'MOVR',
            decimals: 18,
        },
    },
};

export const isConnectedToMoonbeam = async () => {
    const chainId = await provider.request({
        method: 'eth_chainId',
    });
    if (chainId === moonriverChainId) {
        return true
    } else {
        return false
    }
};

export const connect = async (network) => {
    /** In case we need to throw an error, let's grab the error modal & error message */
    const errorModalContainer = document.querySelector('.error-modal-container');
    const errorMessage = document.querySelector('.error-message');

    if (provider) {
        try {
            const currentNetwork = provider.chainId;
            const targetNetwork = supportedNetworks[network];
            if (targetNetwork.chainId === currentNetwork) {
                throw new Error(`You are already connected to the ${targetNetwork.chainName} network`)
            }
            console.log(network)
            await provider.request({ method: 'eth_requestAccounts' });
            await provider.request({
                method: 'wallet_addEthereumChain',
                params: [targetNetwork],
            });
        } catch (e) {
            /** Code 4001 is user rejected, we don't need to notify the user if they rejected the request */
            if (e.code !== 4001) {
                errorModalContainer.style.display = 'block';
                errorMessage.innerHTML = e.message;
            }
        }
    } else {
        errorModalContainer.style.display = 'block';
        errorMessage.innerHTML = `It looks like MetaMask hasn't been installed. Please <a href="https://metamask.io/download.html" target="_blank" rel="noreferrer noopener">install MetaMask</a> and try again.`;
    }
};