import Web3 from "web3";
import Allowances from "./components/Allowances";
import { connect, isConnectedToMoonbeam } from "./helpers/helpers";
import Logo from "./images/moonswap_new.png";
import React from "react";
import { useState } from "react";

const web3 = new Web3(Web3.givenProvider);

function App() {

  const [metaMaskConnected, setmetaMaskConnected] = useState(false)

  async function connectMetaMask() {
    await window.ethereum.enable();
    let networkCheck = await isConnectedToMoonbeam()
    if (networkCheck !== true) {
      await connect("moonriver")
    }
    setmetaMaskConnected(true)
  }

  return (
    <div
      className="wrapper h-screen overflow-auto"
      style={{
        background:
          "radial-gradient(circle, rgb(230, 0, 122) 0%, rgb(4, 4, 4) 100%)",
      }}
    >
      <div className="flex items-center justify-center">
        <div className="mx-2 my-5 text-center">
          <img className="hover:scale-105 mx-auto
          transform
          transition-all
          duration-500 w-4/12" src={Logo} alt="Logo" />
          <h1 className="text-gray-100 font-extrabold text-4xl xs:text-5xl md:text-6xl justify-center">
            Moonriver Token Allowances
          </h1>
          <div className="flex items-center justify-center mt-5">
            {
              metaMaskConnected ? <Allowances web3={web3} /> : <button onClick={connectMetaMask}
                className="
                  p-2
                  my-5
                  font-bold
                  text-white
                  rounded
                  border-2 border-white
                  hover:bg-black hover:bg-opacity-5
                  shadow-md
                  transition
                  duration-500
                  md:text-xl
                  mx-auto
                "
              >
                Connect MetaMask
              </button>
            }
          </div>
        </div>


      </div>
    </div>
  );
}

export default App;
