import React from 'react'
import { useState, useEffect } from 'react'
import { getQuery, getApproveTransactions, getName, getEtherScanPage } from "../helpers/helpers";
import knownApprovers from '../helpers/knownApprovers';
import knownRugs from '../helpers/knownRugs';

import Allowance from './Allowance'
import Loading from './Loading'


function Allowances(props) {

  const [loading, setLoading] = useState(true)
  const [loadingMessage, setloadingMessage] = useState("Loading Contracts")
  const [chain, setChain] = useState(null)
  const [transactions, setTransactions] = useState([])
  const [account, setAccount] = useState(null)

  useEffect(() => {
    const initialise = async () => {
      setLoading(true)
      let account = null;

      try {
        try {
          const accounts = await props.web3.eth.requestAccounts()
          account = accounts[0]
          setAccount(accounts[0])
        } catch (error) {
          const accounts = await window.ethereum.enable();
          account = accounts[0];
        }
        const chainId = await props.web3.eth.getChainId()
        setChain(chainId)

        const query = getQuery(chainId, account)
        setloadingMessage("Getting transactions")
        let txs = await getApproveTransactions(query)
        setloadingMessage("Cleaning contracts with null approvals")
        const cleanFilter = txs.filter(clean => clean.allowance === "CLEAN")
        // There's a bug in this function. If you revoke approval and then approve it again. It won't show
        // One to fix in the future
        cleanFilter.forEach(value => {
          txs = txs.filter((val) => {
            return !(val.contract === value.contract && val.approved === value.approved);
          })

        });

        setloadingMessage("Getting contract names")
        for (const index in txs) {
          txs[index].contractName = await getName(txs[index].contract);
          if (txs[index].contract in knownRugs) {
            txs[index].contractName = "[RUG] " + txs[index].contractName
          }
          // Not running this second call. It's broken.
          // Example https://blockscout.moonriver.moonbeam.network/address/0x0d0ac50741f2Aed4D19325bE385EBeFe49C0d186/transactions has no name
          // txs[index].approvedName = await getName(txs[index].approved);
          // bypassing it below
          if (txs[index].approved in knownApprovers) {
            txs[index].approvedName = knownApprovers[txs[index].approved]
          } else if (txs[index].approved in knownRugs) {
            txs[index].approvedName = "[RUG] " + knownRugs[txs[index].approved]
          }
          else {
            txs[index].approvedName = txs[index].approved
          }
        }

        setTransactions(txs)

      } catch (error) {
        console.log("There was most definitely an error and I don't know how to display this just yet")
      }

      setLoading(false)
    }
    initialise()
  }, [])


  if (loading) {
    return <Loading message={loadingMessage}></Loading>
  } else {
    let elements
    if (transactions.length > 0) {
      const etherscanUrl = getEtherScanPage(chain)
      elements = transactions.map((tx, index) => {
        return <Allowance etherscanURL={etherscanUrl} tx={tx} web3={props.web3} id={tx.contract} account={account} key={index} />
      })
    }

    return (
      <table className="border-collapse w-5/6 text-gray-800 ">
        <thead>
          <tr className="border-b-2  font-bold text-gray-100 bg-pink-600">
            <th class="p-3 hidden md:table-cell">Contract</th>
            <th class="p-3 hidden md:table-cell">Approved Contract</th>
            <th class="p-3 hidden md:table-cell">Allowance</th>
            <th class="p-3 hidden md:table-cell">Actions</th>
          </tr>
        </thead>
        <tbody>
          {elements}
        </tbody>
      </table>
    )
  }
}

export default Allowances
